/* 
 * Whitespace Reset */
* {
  padding: 0;
  margin: 0; }

/* 
 * Box-sizing fix */
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

/* 
